<template>
  <div class="ratingselect">
  	<div class="rating-type">
  		<span @click="select(2)" class="block positive" :class="{'active':selectType===2}">{{desc.all}}<span class="count">{{ratings.length}}</span></span>
  		<span @click="select(0)" class="block positive" :class="{'active':selectType===0}">{{desc.positive}}<span class="count">{{positive.length}}</span></span>
  		<span @click="select(1)" class="block negative" :class="{'active':selectType===1}">{{desc.negative}}<span class="count">{{negative.length}}</span></span>
  	</div>
  	<div @click="toggleContent" class="switch" :class="{'on':onlyContent}">
  		<span class="icon-check_circle"></span>
  		<span class="text">只看有内容的评价</span>
  	</div>
  </div>
</template>

<script>
const POSITIVE =0;
const NEGATIVE =1;
const ALL=2;

export default {
	props:{
		ratings:{
			type:Array,
			default(){
				return [];
			}
		},
		// 选择的类型
		selectType:{
			type:Number,
			default:ALL
		},
		onlyContent:{
			type:Boolean,
			default:false
		},
		desc:{
			type:Object,
			default(){
				return{
					all:'全部',
					positive:'满意',
					negative:'不满意'
				};
			}
		}
	},
	methods:{
		select(n){
			this.$emit('select',n);
		},
		toggleContent(){
			this.$emit('toggleContent');
		}
	},
	computed:{
		positive(){
			// 过滤rating下的rateType，得到数组
			return this.ratings.filter((rating)=>{
				return rating.rateType === POSITIVE;
			})
		},
		negative(){
			return this.ratings.filter((rating)=>{
				return rating.rateType === NEGATIVE;
			})
		}
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ratingselect{

}
.rating-type{
	padding: 18px 0;
	margin: 0 18px;
	font-size: 0;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.block{
	display: inline-block;
	padding:8px 12px;
	margin-right: 8px;
	line-height: 16px;
	border-radius: 1px;
	font-size: 12px;
	color: rgb(77,85,93);
}
.rating-type > .active{
	color:#fff;
	background: rgb(0,160,220);
}
.rating-type > .active:last-child{
	background: rgb(77,85,93);
}
.count{
	margin-left: 2px;
	font-size: 8px;
}
.positive{
	background: rgba(0,160,220,0.2);
}
.negative{
	background: rgba(77,85,93,0.2);
}
.switch{
	padding:12px 18px;
	line-height:24px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	color: rgb(147,153,159);
	font-size: 0;
}
.switch >.icon-check_circle{
	margin-left: 4px;
	font-size:24px;
	display: inline-block;
	vertical-align: top;
}
.switch >.text{
	vertical-align: top;
	font-size:12px;
}
.on > .icon-check_circle{
	color: #00c850;
}
</style>



