<template>
 <transition name="fade">
     <div v-show="showFlag" class="food" ref="food">
     	<div class="food-content">
     		<div class="image-header">
     			<img :src="food.image">
     			<div class="back" @click="hide"><i class="icon-arrow_lift"></i></div>
     		</div>
     		<div class="content">
     			<h1 class="title">{{food.name}}</h1>
     			<div class="detail">
     				<span class="sell-count">月售{{food.sellCount}}</span>
     				<span class="rating">好评率{{food.rating}}%</span>
     			</div>
     			<div class="price">
  					<span>￥{{food.price}}</span>
  					<span v-show="food.oldPrice">{{food.oldPrice}}</span>
  				</div>
  				<div class="cartcontrol-wrapper">
     			    <cartcontrol :food="food" @cartAdd="cartAdd"></cartcontrol>
     		    </div>
     		    <transition name="buy">
     		        <div class="buy" v-show="!food.count||food.count===0" @click.stop="addFirst">加入购物车</div>
     		    </transition>
     	    </div>
     	    <split v-show="food.info"></split>
     	    <div class="info" v-show="food.info">
     	    	<h1 class="title">商品信息</h1>
     	    	<p class="text">{{food.info}}</p>
     	    </div>
     	    <split></split>
     	    <div class="rating">
     	    	<h1 class="title">商品评价</h1>
     	    	<ratingselect :select-type="selectType" :only-content="onlyContent" :desc="desc" :ratings="food.ratings" @select="selectRatingselect" @toggleContent="toggleContent"></ratingselect>
     	    	<!-- 评价列表 -->
     	    	<div class="rating-wrapper">
     	    		<ul v-show="food.ratings && food.ratings.length">
     	    			<li v-show="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings" class="rating-item">
     	    				<div class="user">
     	    					<span class="name">{{rating.username}}</span>
     	    					<img class="avatar" width="12" height="12" :src="rating.avatar">
     	    				</div>
     	    				<div class="time">{{rating.rateTime |formatDate}}</div>
     	    				<p class="text">
     	    					<span :class="{'icon-thumb_up':rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>{{rating.text}}
     	    				</p>
     	    			</li>
     	    		</ul>
     	    		<div class="no-rating" v-show="!food.ratings ||!food.ratings.length">暂无评价</div>
     	    	</div>
     	    </div>
        </div>
    </div>
 </transition>
</template>

<script>
// 引入better-scroll组件库
import BScroll from'better-scroll';
import cartcontrol from '../cartcontrol/cartcontrol.vue';
import Vue from 'vue';
// 引入一个通过export配置的function用{a,b}引入
import {formatDate} from '../../common/js/date.js'
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
const POSITIVE =0;
const NEGATIVE =1;
const ALL=2;
export default {
	components:{
		cartcontrol,
		split,
		ratingselect
	},
	props:{
		food:{
			type:Object
		},
	},
	data(){
		return {
		showFlag:false,
		selectType:ALL,
		onlyContent:true,
		desc:{
			    all:'全部',
				positive:'推荐',
				negative:'吐槽'
			}
		}
	},
	methods:{
		// 展示food组件
		show(){
			this.showFlag=true;
			this.selectType=ALL;
			this.onlyContent=true;
			this.$nextTick(()=>{
				if(!this.scroll){
		     // 设置BScroll参数click:true
            this.scroll = new BScroll(this.$refs.food,{click:true});
        }else{
        	this.scroll.refresh();
        }
    });
		},
		hide(){
			this.showFlag=false
		},
		addFirst(){
		   this.$emit('cartAdd',event.target);
           Vue.set(this.food,'count',1);
		},
		// 传递事件给父组件，实现小球抛物线效果
		cartAdd(){
		this.$emit('cartAdd',event.target);
		},
		selectRatingselect(n){
			this.selectType=n;
		},
		toggleContent(){
			this.onlyContent=!this.onlyContent;
		},
		needShow(type, text){
			// 判断是否只显示有内容的评价
			if(this.onlyContent && !text){
				return false;
			}
			// 判断是否显示所有评论
			if(this.selectType === ALL){
				return true;
			}else{
				// 返回selectType与type相同的，才显示
				return type === this.selectType;
			}
		}
	},
	// 过滤,转换日期格式
	filters:{
		formatDate(time){
			let date = new Date(time);
			// 格式转换
			return formatDate(date, 'yyyy-MM-dd hh:mm');
		}
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.food{
	position: fixed;
	left: 0;
	top:0;
	bottom: 48px;
	z-index: 30;
	width: 100%;
	background: #fff;
}
.fade-enter-active, .fade-leave-active {
  transition: all .3s linear;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  transform: translate3d(100%,0,0);
}
.image-header{
	position: relative;
	width: 100%;
	height: 0;
	/*黑魔法！！ 100%就是宽度的百分百,所以宽高相等*/
	padding-top:100%;
}
.image-header > img{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100;
}
.back{
	position: absolute;
	top: 10px;
	left: 0;
}
.icon-arrow_lift{
    display: block;
    padding: 10px;
    font-size: 20px;
    color: #fff;
}
.content{
	padding:18px;
	position: relative;
}
.content > .title{
	line-height: 14px;
	margin-bottom: 8px;
	font-size: 14px;
	font-weight: 700;
	color: rgb(7,17,27);
}
.content >.detail{
	margin-bottom: 18px;
	line-height: 10px;
	font-size: 0;
}
.content >.detail > .sell-count,.content >.detail >.rating{
	font-size: 10px;
	color: rgb(147,153,159);
}
.content >.detail > .sell-count{
	margin-right: 12px;
}

.content > .price > span{
	font-size: 14px;
	color: rgb(240,20,20);
	font-weight: 700;
	line-height: 24px;
}
.content >.price > span:last-child{
	font-size: 10px;
	color: rgb(147,153,159);
	margin-left: 8px;
	text-decoration: line-through;
	font-weight: 700;
	line-height: 24px;
}
.cartcontrol-wrapper{
	position: absolute;
	right: 12px;
	bottom: 12px;
}
.buy{
	position: absolute;
	right: 18px;
	bottom:18px;
	z-index: 10;
	height: 24px;
	line-height: 24px;
	padding:0 12px;
	box-sizing: border-box;
	font-size: 10px;
	border-radius: 12px;
	color: #fff;
	background: rgb(0,160,220);
}
.buy-enter-active, .buy-leave-active {
  transition: all .3s;
}
.buy-enter, .buy-leave-to /* .buy-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.info{
	padding:18px;
}
.info > .title{
	line-height: 14px;
	margin-bottom: 6px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.info > .text{
	color: rgb(77,85,93);
	font-size: 12px;
	font-weight: 200;
	line-height: 24px;
	padding:0 8px;
}
.rating{
	padding-top: 18px;
}
.rating > .title{
	line-height: 14px;
	margin-left:18px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.rating-wrapper{
	padding:0 18px;
}
.rating-wrapper > ul>.rating-item{
	position: relative;
	padding: 16px 0;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.rating-wrapper > ul>.rating-item >.user{
	position: absolute;
	right: 0;
	top: 16px;
	line-height:12px;
	font-size: 0;
}
.rating-wrapper > ul>.rating-item >.user >.name{
	display: inline-block;
	margin-right: 6px;
	vertical-align: top;
	font-size: 10px;
	color: rgb(147,153,159);
}
.rating-wrapper > ul>.rating-item >.user > .avatar{
	border-radius: 50%;
}
.rating-wrapper > ul>.rating-item > .time{
	margin-bottom: 6px;
	line-height:12px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.rating-wrapper > ul>.rating-item > .text{
	line-height:16px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.icon-thumb_up,.icon-thumb_down{
	margin-right: 4px;
	line-height: 16px;
	font-size: 12px;
}
.icon-thumb_up{
	color: rgb(0,160,220);
}
.icon-thumb_down{
	color: rgb(147,153,159);
}
.no-rating{
	padding:16px 0;
	font-size: 12px;
	color: rgb(147,157,159);
}
</style>



