<template>
  <div class="cartcontrol">
    <transition name="move">
  	    <div class="cart-decrease icon-remove_circle_outline" v-show="food.count>0" @click.stop="decrCart"></div>
  	</transition>
  	<div class="cart-count" v-show="food.count>0">{{food.count}}</div>
  	<div class="cart-add icon-add_circle" @click.stop="addCart"></div>

  </div>
</template>

<script>
import Vue from 'vue'

export default {
	props:{
		food:{
			type:Object
		}
	},
	created(){
		 // console.log(this.food)
	},
	methods:{
		addCart(event){
			if (!this.food.count) {
				// 用Vue.set为food添加count属性
				Vue.set(this.food, 'count',1)
			}else{
				this.food.count++;
			}
			// 同时将发生点击事件的event.target传递给父组件
			this.$emit('cartAdd',event.target);
			// console.log(this.food.count)
		},
		decrCart(){
			if(this.food.count){
				this.food.count--;
			}
		}
	}
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.cartcontrol{
	font-size: 0;
}
.cartcontrol > .cart-decrease,.cartcontrol > .cart-add{
	display: inline-block;
	padding: 6px;
	font-size: 24px;
	line-height: 24px;
	color: rgb(0,165,220); 
}
.cartcontrol > .cart-count{
	display: inline-block;
	vertical-align: top;
	width: 12px;
	padding-top: 6px;
	line-height: 24px;
	text-align: center;
	font-size: 10px;
	color:rgb(147,153,159);
}
.cartcontrol > .cart-add{
}
.move-enter-active, .move-leave-active {
  transition: all .5s linear;
}
.move-enter, .move-leave-to /* .move-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translate3d(40px) rotate(180deg);
}
</style>



