<template>
  <div id="app">
  <v-header :seller="seller"></v-header>
  <div class="tab border-1px">
  	<div class="tab-item">
         <router-link :to="{path:'/goods'}" >商品</router-link>
  	</div>
  	<div class="tab-item">
  		<router-link :to="{path:'/ratings'}">评价</router-link>
  	</div>
  	<div class="tab-item">
  		<router-link :to="{path:'/seller'}">商家</router-link>
  	</div> 
  </div>
 <!-- 访问过的组件进行缓存<keep-alive> -->
    <keep-alive>
      <router-view :seller="seller"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import header from './components/header/header';
import {urlParse} from './common/js/utill.js'
// 定义常量
const ERR_OK =0 ;

export default {
  data() {
     return {
      seller:{
        id: (()=>{
          let queryParam = urlParse();
          // console.log(queryParam);
          return queryParam.id;
        })()
      },
      // response:{}
     };
  },
  // 从服务器获取数据
  created:function(){
    this.$http.get('https://www.easy-mock.com/mock/5ba742b7774ea0691ff86e85/api/seller?id=' + this.seller.id)
    // 使用箭头函数
    .then((res)=>{
       res = res.body;
         if(res.errno === ERR_OK){
         // 给对象扩展属性，从而不覆盖掉id属性
          this.seller = Object.assign({},this.seller,res.data);
          // console.log(this.seller.id)          
         }
    },(err)=>{
        console.log(err)
    })
  },
  components: {
  	vHeader:header
  }
};
</script>

<style>
 #app .tab {
  display: flex;
  width: 100%;
  height:40px;
  line-height: 40px;
  position:relative;
}
#app .tab:after{
  display:block;
  position:absolute;
  left:0;
  bottom:0;
  width:100%;
  border-top:1px solid rgba(7,17,27,0.1);
  content:' ';
}
#app .tab .tab-item{
  flex:1;
  text-align: center;
}
#app .tab .tab-item > a{
  display: block;
  font-size: 14px;
  color: rgb(77,85,93);
}
#app .tab .tab-item > .router-link-active{
  color: rgb(240,20,20);
}           
</style>



