<template>
  <div class="seller" ref="seller">
  	<div class="seller-content">
  		<div class="overview">
  			<h1 class="title">{{seller.name}}</h1>
  			<div class="desc">
  				<star :size="36" :score="seller.score"></star>
  				<span class="text">({{seller.ratingCount}})</span>
  				<span class="text">月售{{seller.sellCount}}单</span>
  			</div>
  			<ul class="remark">
  				<li class="block">
  					<h2>起送价</h2>
  					<div class="content">
  						<span class="stress">{{seller.minPrice}}</span>
  						元
  					</div>
  				</li>
  				<li class="block">
  					<h2>商家配送</h2>
  					<div class="content">
  						<span class="stress">{{seller.deliveryPrice}}</span>元
  					</div>
  				</li>
  				<li class="block">
  					<h2>平均配送时间</h2>
  					<div class="content">
  						<span class="stress">{{seller.deliveryTime}}</span>分钟
  					</div>
  				</li>
  			</ul>
  			<!-- 收藏 -->
  			<div class="favorite" @click="toggleFavorite">
  				<span class="icon-favorite" :class="{'active':favorite}"></span>
  				<span class="text">{{favoriteText}}</span>
  			</div>
  		</div>
  		<split></split>
  		<div class="bulletin">
  			<h1 class="title">公告与活动</h1>
  			<div class="contnet-wrapper">
  				<p class="content">{{seller.bulletin}}</p>
  			</div>
  			<ul v-if="seller.supports" class="supports">
			    <li class="supports-item" v-for="(item,index) in seller.supports">
			        <span class="icon" :class="classMap[seller.supports[index].type]"></span>
				    <span class="text">{{seller.supports[index].description}}</span>
			    </li>
	        </ul>
  		</div>
  		<split></split>
  		<div class="pics">
  			<h1 class="title">商家实景</h1>
  			<div class="pic-wrapper" ref="picWrapper">
  				<ul class="pic-list" ref="picList">
  					<li class="pic-item" v-for="pic in seller.pics">
  						<img :src="pic" width="120px" height="90px">
  					</li>
  				</ul>
  			</div>
  		</div>
  		<split></split>
  		<div class="info">
  			<h1 class="title">商家信息</h1>
  			<ul>
  				<li class="info-item" v-for="info in seller.infos">
  					{{info}}
  				</li>
  			</ul>
  		</div>
  	</div>
  </div>
</template>

<script>
import BScroll from'better-scroll';
import star from '../star/star.vue';
import split from '../split/split.vue';
import {saveTolocal,loadFromLocal} from '../../common/js/store.js'
export default {
	props:{
		seller:{
			type:Object
		}
	},
	components:{
		star,
		split
	},
	data(){
		return {
			favorite:(()=>{
				return loadFromLocal(this.seller.id,'favorite',false);
			})
		}
	},
	computed:{
		favoriteText(){
			return this.favorite?'已收藏':'收藏'
		}
	},
	// 在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。
	created(){
      this.classMap=['decrease', 'discount','guarantee','invoice','special']
	},
	methods:{
		// 商家图片滚动
		show(){
			if(this.seller.pics){
			   let picWidth = 120;
        	   let margin = 6;
        	   let winth = (picWidth+margin)*this.seller.pics.length - margin;
        	   this.$refs.picList.style.width=winth+'px';
        	   if(!this.picScroll){
        	       this.picScroll = new BScroll(this.$refs.picWrapper,{scrollX:true,eventPassthrough:'vertical'})
        	   }else{
        		this.picScroll.refresh();
        	   }
			}
		},
		toggleFavorite(){
			this.favorite=!this.favorite;
			// 存储
			saveTolocal(this.seller.id,'favorite',this.favorite);
		}
	},
	mounted(){
		this.$nextTick(()=>{
			if(!this.scroll){
		     // 设置BScroll参数click:true
            this.scroll = new BScroll(this.$refs.seller,{click:true});
        }else{
        	this.scroll.refresh();
        }
        // 设置延迟执行方法，解决刷新问题
        setTimeout(() => {
        	this.show();
        }, 20)
    });
	}
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.seller{
	position: absolute;
	top:174px;
	bottom: 0;
	width: 100%;
	overflow: hidden;
}
.seller-content{

}
.seller-content>.overview{
	position: relative;
	padding: 18px;
}
.seller-content>.overview>.favorite{
	position: absolute;
	width:50px;
	right: 11px;
	top: 18px;
	text-align: center;
}
.favorite>.icon-favorite{
	display: block;
	margin-bottom: 4px;
	line-height: 24px;
	font-size: 24px;
	color: #d4d6d9;
}
.seller-content>.overview>.favorite>.active{
	color: rgb(240,20,20);
}
.seller-content>.overview>.favorite>.text{
	line-height: 10px;
	font-size: 10px;
	color: rgb(77,85,93);
}
.seller-content>.overview>.title{
	margin-bottom: 8px;
	line-height: 14px;
	color: rgb(7,17,27);
	font-size: 14px;
}
.seller-content>.overview>.desc{
	padding-bottom: 18px;
	font-size: 0;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.seller-content>.overview>.desc >.star{
	display: inline-block;
	vertical-align: top;
	margin-right: 8px;
}
.seller-content>.overview>.desc > .text{
	margin-right: 12px;
	display: inline-block;
	vertical-align: top;
	line-height: 18px;
	font-size:10px ;
	color: rgb(77,85,93);
}
.remark{
	display: flex;
	padding-top: 18px;
}
.remark >.block{
	flex:1;
	text-align: center;
	border-right: 1px solid rgba(7,17,27,0.1);
}
.remark >.block:last-child{
	border:none;
}
.remark >.block > h2 {
	margin-bottom: 4px;
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.remark >.block > .content{
	line-height: 24px;
	font-size: 10px;
	color: rgb(7,17,27);
}
.remark >.block > .content>.stress{
	font-size: 24px;
}
.bulletin{
	padding:18px 18px 0 18px;
}
.bulletin >.title{
	margin-bottom: 8px;
	line-height: 14px;
	color: rgb(7,17,27);
	font-size: 14px;
}
.bulletin>.contnet-wrapper{
	padding:0 12px 16px 12px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.bulletin>.contnet-wrapper>.content{
	line-height: 24px;
	font-size: 12px;
	color: rgb(240,20,20);
}
.bulletin>.supports{

}
.bulletin>.supports>.supports-item{
	padding:16px 12px;
	font-size: 0;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.bulletin>.supports>.supports-item:last-child{
	border-bottom: none;
}
.bulletin>.supports>.supports-item>.icon{
	display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin-right: 4px;
	background-size: 12px 12px;
	background-repeat: no-repeat;
}
/*活动图标样式*/
.bulletin>.supports>.supports-item>.decrease{
  background-image: url(decrease_4@2x.png);
}
.bulletin>.supports>.supports-item>.discount{
  background-image: url(discount_4@2x.png);
}
.bulletin>.supports>.supports-item>.guarantee{
  background-image: url(guarantee_4@2x.png);
}
.bulletin>.supports>.supports-item>.invoice{
  background-image: url(invoice_4@2x.png);
}
.bulletin>.supports>.supports-item>.special{
  background-image: url(special_4@2x.png);
}
.bulletin>.supports>.supports-item>.text{
	line-height: 16px;
	font-size: 12px;
	font-weight: 200;
	color: rgb(7,17,27);
}
.pics{
	padding:18px;
}
.pics > .title{
	margin-bottom: 12px;
	line-height: 14px;
	color: rgb(7,17,27);
	font-size: 14px;
}
.pics >.pic-wrapper{
	width: 100%;
	overflow: hidden;
	/*文本不换行*/
	white-space: nowrap;
}
.pics >.pic-wrapper>.pic-list{
	font-size: 0;
}
.pics >.pic-wrapper>.pic-list>.pic-item{
	display: inline-block;
	margin-right: 6px;
	width: 120px;
	height: 90px;
}
.pics >.pic-wrapper>.pic-list>.pic-item:last-child{
	margin-right: 0px;
}
.info{
	padding:18px 18px 0 18px;
	color: rgb(7,17,27);
}
.info > .title{
	padding-bottom: 12px;
	line-height: 14px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	font-size: 14px;
}
.info > ul >.info-item{
	padding:16px 12px;
	line-height: 16px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	font-size: 12px;
}
.info>ul >.info-item:last-child{
	border-bottom: none;
}
</style>



