<template>
  <div class="ratings" ref="ratings">
  	<div class="ratings-content">
  		<div class="overview">
  			<div class="overview-left">
  				<h1 class="score">{{seller.score}}</h1>
  				<div class="title">综合评分</div>
  				<div class="rank">高于周边商家{{seller.rankRate}}%</div>
  			</div>
  			<div class="overview-right">
  				<div class="score-wrapper">
  					<span class="title">服务态度</span>
  					<star :size="36" :score="seller.serviceScore"></star>
  					<span class="score">{{seller.serviceScore}}</span>
  				</div>
  				<div class="score-wrapper">
  					<span class="title">商品评分</span>
  					<star :size="36" :score="seller.foodScore"></star>
  					<span class="score">{{seller.foodScore}}</span>
  				</div>
  				<div class="delivery-wrapper">
  					<span class="title">送达时间</span>
  					<span class="delivery">{{seller.deliveryTime}}分钟</span>
  				</div>
  			</div>
  		</div>
  		<split></split>
  		<ratingselect :select-type="selectType" :only-content="onlyContent"  :ratings="ratings" @select="selectRatingselect" @toggleContent="toggleContent"></ratingselect>
  		<div class="rating-wrapper">
  			<ul>
  				<li v-for="rating in ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
  					<div class="avatar">
  						<img :src="rating.avatar" width="28px" height="28px">
  					</div>
  					<div class="content">
  						<h1 class="name">{{rating.username}}</h1>
  						<div class="star-wrapper">
  							<star :size="24" :score="rating.score"></star>
  							<span class="delivery" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
  						</div>
  						<p class="text">{{rating.text}}</p>
  						<div class="recommend" v-show="rating.recommend && rating.recommend.length">
  							<span class="icon-thumb_up"></span>
  							<span v-for="item in rating.recommend" class="item">{{item}}</span>
  						</div>
  						<div class="time">
  							{{rating.rateTime| formatDate}}
  						</div>
  					</div>
  				</li>
  			</ul>
  		</div>
  	</div>
  </div>
</template>

<script>
import BScroll from'better-scroll';
import star from '../star/star.vue'
import split from '../split/split.vue';
import ratingselect from '../ratingselect/ratingselect.vue';
import {formatDate} from '../../common/js/date.js'
const ALL=2;
const ERR_OK = 0;
export default {
	props:{
		seller:{
			type:Object
		}
	},
	data(){
		return {
		    ratings:[],
		    selectType:ALL,
		    onlyContent:true,
		}
	},
	created(){
		this.$http.get('https://www.easy-mock.com/mock/5ba742b7774ea0691ff86e85/api/ratings')
		.then((res)=>{
			res=res.body;
			
			if(res.errno===ERR_OK){
				this.ratings=res.data;
				// console.log(this.ratings)
				// 初始化视图
		this.$nextTick(()=>{
				if(!this.scroll){
		     // 设置BScroll参数click:true
            this.scroll = new BScroll(this.$refs.ratings,{click:true});
        }else{
        	this.scroll.refresh();
          }
		})


			}
		});
	},
	components:{
		star,
		split,
		ratingselect
	},
	methods:{
		selectRatingselect(n){
			this.selectType=n;
		},
		toggleContent(){
			this.onlyContent=!this.onlyContent;
		},
		needShow(type, text){
			// 判断是否只显示有内容的评价
			if(this.onlyContent && !text){
				return false;
			}
			// 判断是否显示所有评论
			if(this.selectType === ALL){
				return true;
			}else{
				// 返回selectType与type相同的，才显示
				return type === this.selectType;
			}
		}
	},
	// 过滤,转换日期格式
	filters:{
		formatDate(time){
			let date = new Date(time);
			// 格式转换
			return formatDate(date, 'yyyy-MM-dd hh:mm');
		}
	}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.ratings{
	position: absolute;
	top:174px;
	bottom: 0;
	width: 100%;
	overflow: hidden;
}
.overview{
	display: flex;
	padding: 18px 0;
}
.overview-left{
	flex:0 0 137px;
	padding: 6px 0;
	width: 137px;
	border-right: 1px solid rgba(7,17,27,0.1);
	text-align: center;
}
@media only screen and (max-width: 320px){
	.overview-left{
	flex:0 0 120px;
	padding: 6px 0;
	width: 120px;
	border-right: 1px solid rgba(7,17,27,0.1);
	text-align: center;
}
.overview>.overview-right{
	flex: 1;
	padding-left:6px;
}
}


.overview-left >.score{
	margin-bottom: 6px;
	line-height: 28px;
	font-size: 24px;
	color: rgb(255,153,0);
}
.overview-left > .title{
	margin-bottom: 8px;
	line-height: 12px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.overview-left > .rank{
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.overview-right{
	flex: 1;
	padding:6px 0px 6px 24px;
}
.overview-right > .score-wrapper{
	margin-bottom: 8px;
	font-size: 0;
}
.overview-right > .score-wrapper >.title{
	display: inline-block;
	line-height: 18px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.overview-right > .score-wrapper >.star{
	display: inline-block;
	margin: 0 12px;
	vertical-align: top;
}
.overview-right > .score-wrapper > .score{
	display: inline-block;
	line-height: 18px;
	font-size: 12px;
	color: rgb(255,153,0);
}
.delivery-wrapper{

}
.delivery-wrapper > .title{
	line-height: 18px;
	font-size: 12px;
	color: rgb(7,17,27);
}
.delivery-wrapper > .delivery{
	margin-left: 12px;
	font-size: 12px;
	color: rgb(147,153,159);
}
.rating-wrapper{
	padding:0 18px;
}
.rating-item{
	display: flex;	
	padding:18px 0;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.rating-item>.avatar{
	flex:0 0 28px;
	width: 28px;
	margin-right: 12px;
}
.rating-item>.avatar > img{
	border-radius: 50%;
}
.rating-item> .content{
	position: relative;
	flex: 1;
}
.rating-item> .content > .name{
	margin-bottom: 4px;
	line-height: 12px;
	font-size: 10px;
	color: rgb(7,17,27);
}
.rating-item> .content > .star-wrapper{
	margin-bottom: 6px;
	font-size: 0;
}
.rating-item> .content > .star-wrapper >.star{
	display: inline-block;
	margin-right: 6px;
	vertical-align: top;
}
.rating-item> .content > .star-wrapper >.delivery{
	display: inline-block;
	vertical-align: top;
	font-size: 10px;
	font-weight: 200;
	color: rgb(147,153,159);
	line-height: 12px;
}
.rating-item> .content >.text{
	line-height: 18px;
	color: rgb(7,17,27);
	margin-bottom: 8px;
	font-size: 12px;
}
.rating-item> .content >.recommend{
	line-height: 16px;
	font-size: 0;
}
.rating-item> .content >.recommend > .icon-thumb_up,.rating-item> .content >.recommend > .item{
	display: inline-block;
	margin: 0 8px 4px 0;
	font-size: 9px;
}
.rating-item> .content >.recommend > .icon-thumb_up{
	color: rgb(0,160,220);
}
.rating-item> .content >.recommend > .item{
	padding:0 6px;
	border: 1px solid rgba(7,17,27,0.1);
	border-radius: 1px;
	color: rgb(147,153,159);
background: #fff;
}
.rating-item> .content >.time{
	position: absolute;
	top: 0;
	right: 0;
	line-height: 12px;
	font-size: 10px;
	color: rgb(147,153,159);
}
</style>



