<template>
    <div>
  <div class="shopcart">
     <div class="content" @click="toggleList">
     	<div class="content-left">
     		<div class="logo-wrapper">
     			<div class="logo" :class="{'highlight':totalCount>0}">
     				<i class="icon-shopping_cart" :class="{'highlight':totalCount>0}"></i>
     			</div>
     			<div class="num">{{totalCount}}</div>
     		</div>
     		<div class="price">￥{{totalPrice}}</div>
     		<div class="desc">另需配送费{{deliveryPrice}}元</div>
     	</div>
     	<div class="content-right" @click.stop="pay">
     		<div class="pay" :class="payClass">{{payDesc}}</div>
     	</div>
     </div>
         <!-- 小球滑落动画 -->
        <div class="ball-container"> 
            <transition-group name="drop" tag="div"
            v-on:before-enter="beforeEnter"
            v-on:enter="dropEnter"
            v-on:after-enter="afterEnter">
               <div v-for="(ball,index) in balls" v-show="ball.show" class="ball" :key="index">
                   <div class="inner inner-hook"></div>
               </div>
            </transition-group>
        </div>
    <transition name="flod">
        <div class="shopcart-list" v-show="listShow" ref="listShow">
            <div class="list-header">
                <h1 class="title">购物车</h1>
                <span class="empty" @click="empty">清空</span>
            </div>
            <div class="list-content" ref="flodWrapper">
                <ul>
                    <li class="food" v-for="food in selectFoods">
                        <span class="name">{{food.name}}</span>
                        <div class="price">
                            <span>￥{{food.price*food.count}}</span>
                        </div>
                        <div class="cartcontrol-wrapper">
                            <cartcontrol :food="food" ></cartcontrol>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </transition>
  </div>
  <!-- 购物车背景 -->
  <transition name="fade">
  <div class="list-mast" v-show="listShow" @click="noList"></div>
 </transition>
</div>
</template>

<script>
import cartcontrol from '../cartcontrol/cartcontrol.vue';
import BScroll from'better-scroll';
export default {
     components: {
    cartcontrol
  },
	props:{
	 deliveryPrice:{
		type:Number
		},
    minPrice:{
    	type:Number
    },
    selectFoods:{
    	type:Array,
    	default() {
    	 return [
    	   {
              price:10,
              count:1
    	   },
    	 ];
    	}
    }
	},
     data(){
        return{
            // 定义五个小球
             balls:[
               {
                show:false,el: null
               },
               {
                show:false,el: null
               },
               {
                show:false,el: null
               },
               {
                show:false,el: null
               },
               {
                show:false,el: null
               },
             ],
             dropBalls:[],
             fold:true
        }
     },
	 computed:{
	 	// 商品总价
	 	totalPrice(){
	 		let total = 0;
	 		/*this.selectFoods.forEach((food)=>{
	 			total += food.price *food.count;
	 		});*/
	 		this.selectFoods.forEach(function (food) {
	 			total += food.price * food.count
	 		})
	 		return total;
	 	},
	 	// 商品数量总和
	 	totalCount(){
	 		let count =0 ;
	 		this.selectFoods.forEach(function (food){
	 			count +=food.count
	 		})
	 		return count;
	 	},
        payDesc(){
            if(this.totalPrice ===0){
                return "￥"+this.minPrice+"元起送";
            }else if(this.totalPrice < this.minPrice){
                let diff = this.minPrice-this.totalPrice
                return "还差￥"+diff+"元起送";
            }else{
                return '去结算';
            }
        },
        payClass(){
            if(this.totalPrice < this.minPrice){
                return 'not-enough';
            }else {
                return 'enough';
            }
        },
        listShow(){
            if(!this.totalCount){
                this.fold=true;
                return false
            }
            let show = !this.fold;
          if(show){
                this.$nextTick(function () {
                    if(!this.scroll){
                        this.flodScroll = new BScroll(this.$refs.flodWrapper,{click:true});
                    }else {
                       this.flodScroll.refresh();
            }
                });
            }
            return show;
        }
	 },
    methods:{
        // 通过父元素cartAdd（）调用 ,小球动画方法,接收el DOM元素
        drop(el) {
            // getBoundingClientRect()接口获取el元素的位置
            /*console.log(el.getBoundingClientRect());*/
            for (let i=0;i<this.balls.length;i++){
                let ball =this.balls[i];
                if (!ball.show) {
                    ball.show = true;
                    ball.el=el;
                    this.dropBalls.push(ball);
                    return;
                }
            }
            },
            // 进入之前
beforeEnter (el, done) {
let count = this.balls.length;
while (count--) {
let ball = this.balls[count];
if (ball.show) {
let rect = ball.el.getBoundingClientRect();
let x = rect.left - 32;
let y = -(window.innerHeight - rect.top - 22);
/*el.style.display = '';*/
el.style.transform = `translate3d(0,${y}px,0`;
el.style.webkitTransform = `translate3d(0,${y}px,0`;
let inner = el.getElementsByClassName('inner-hook')[0];
inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
inner.style.transform = `translate3d(${x}px,0,0)`;
// console.log(el);
}
}
},
// 进入，必须要有done()回调，
dropEnter (el, done) {
/* eslint-disable no-unused-vars */
/* 触发浏览器重绘; */
let rf = el.offsetHeight;
this.$nextTick(() => {
el.style.webkitTransform = 'translate3d(0, 0, 0)';
el.style.transform = 'translate3d(0, 0, 0)';
let inner = el.getElementsByClassName('inner-hook')[0];
inner.style.webkitTransform = 'translate3d(0, 0, 0)';
inner.style.transform = 'translate3d(0, 0, 0)';
el.addEventListener('transitionend', done);
// done();
});
// console.log(el);
// done();
},
// 进入之后
afterEnter (el) {
el.style.display = 'none';
let ball = this.dropBalls.shift();
ball.show = false;
ball.el = null;
},
toggleList(){
    if(!this.totalCount){
        return;
    }
    this.fold = !this.fold;
    },
noList(){
    this.fold=true;
},
    // 清空count
    empty(){
      this.selectFoods.forEach(function(food){
            food.count = 0;
      })
    },
    pay(){
        if(this.totalPrice<this.minPrice){
            return;
        }else{
            window.alert("支付"+this.totalPrice+"元")
        }
    }
},

mounted: function () {
  this.$nextTick(function () {
    // this._initScroll();
    // Code that will run only after the
    // entire view has been rendered
  })
}
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.shopcart{
	position: fixed;
	left: 0;
	bottom:0px;
	z-index: 50;
	width: 100%;
	height: 48px;

}
.content{
	display: flex;
	background: #141d27;
	font-size: 0;
	color: rgba(255,255,255,0.4);
}
.content-left{
  flex: 1;
}
.content-left > .logo-wrapper{
    display: inline-block;
    vertical-align: top;
    position: relative;
    top:-10px;
    padding: 6px;
    margin: 0 12px;
    width: 56px;
    height: 56px;
    box-sizing: border-box;
    border-radius: 50%;
    background: #141d27;
}
.content-left > .logo-wrapper > .logo{
	width: 100%;
	height: 100%;
    border-radius: 50%;
	background: #2b343c;
	 text-align: center;
}
  .content-left > .logo-wrapper > .highlight{
	background: rgb(0,160,220);
}  
.logo-wrapper > .num{
	position: absolute;
	top:0;
	right: 0;
	width: 24px;
	height: 16px;
	line-height:16px;
	text-align: center;
	border-radius: 16px;
	font-size: 9px;
	font-weight: 700;
	color: #fff;
	background: rgb(240,20,20);
	box-shadow: 0px 4px 8px 0px rgba(0,0,0,0.4);
}
 .logo-wrapper > >.logo>.highlight{
	color: #fff;
} 
.icon-shopping_cart{
    font-size:24px;
    color:#80858a;
    line-height: 44px;
}
.content-left > .price{
    display: inline-block;
    vertical-align: top;
    margin-top: 12px;
    line-height:24px;
    box-sizing: border-box;
    padding-right: 12px;
   border-right: 1px solid rgba(255,255,255,0.1);
   font-size: 16px;
   font-weight: 700;
}
.content-left > .desc{
    display: inline-block;
    vertical-align: top;
    margin: 12px 0 0 12px;
    line-height: 24px;
    vertical-align: top;
    font-size: 10px;
}
.content-right{
  flex:0 0 105px;
  width: 105px;
}
.content-right > .pay{
	height: 48px;
	line-height:48px;
	text-align: center;
	font-size: 12px;
	font-weight: 700;
}
.content-right > .not-enough{
    background: #2b333b;
}
.content-right > .enough{
    background: #00b43c;
    color: #fff;
}
.ball-container{

}
.ball{
    position: fixed;
    left: 32px;
    bottom: 22px;
    z-index: 200;
}
.inner {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: rgb(0,160,220);
    transition: all 0.4s linear;
}
.drop-enter-active{
    transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41);
}
/*购物列表*/
.shopcart-list{
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    transform: translate3d(0,-100%,0);
}
.list-header{
    height:40px;
    line-height:40px;
    padding:0 18px;
    background: #f3f5f7;
    border-bottom: 1px solid rgba(7,17,27,0.1)
}
.title{
    float: left;
    font-size:14px;
    color: rgb(7,17,27)
}
.empty{
    float: right;
    font-size: 12px;
    color: rgb(0,160,220)
}
.list-content{
    padding:0 18px;
    max-height: 217px;
    background: #fff;
    overflow: hidden;
}
.list-content > ul >.food{
    position: relative;
    padding:12px 0;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(7,17,27,0.1)
}
.list-content > ul >.food >.name{
    line-height:24px;
    font-size: 14px;
    color: rgb(7,17,27)
}
.list-content > ul >.food > .price{
    position: absolute;
    right: 90px;
    bottom: 12px;
    line-height: 24px;
    color: rgb(240,20,20);
    font-size: 14px;
    font-weight: 700;
}
.list-content > ul >.food >.cartcontrol-wrapper{
    position: absolute;
    right: 0;
    bottom:6px;
}
.flod-enter-active, .flod-leave-active {
  transition: all 0.5s; 
}
.flod-enter, .flod-leave-to /* .flod-leave-active below version 2.1.8 */ {
  transform: translate3d(0,0,0);
}
.list-mast{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 40;
    filter:blur(20px);
    background: rgba(7,17,27,0.6);
}
.fade-enter-active, .fade-leave-active {
  transition: all 0.5s; 
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



