<template>
  <div class="goods">
  	<div class="menu-wrapper" ref="menuWrapper">
  		<ul>
  			<li v-for="(item,index) in goods" class="menu-item" :class="{'current':courrentIndex === index}"
  			 @click="selectMenu(index)">
  				<span class="text">
  			    <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
  			    {{item.name}}
  			    </span>
  		    </li>
  		</ul>
  	</div>
  	<div class="foods-wrapper" ref="foodsWrapper">
  		<ul>
  			<li class="food-list food-list-hook" v-for="item in goods">
  				<h1 class="title">{{item.name}}</h1>
  				<ul>
  					<li @click="selectFood(food)" v-for="food in item.foods" class="food-item">
  						<div class="icon">
  							<img :src="food.icon">
  						</div>
  						<div class="content">
  							<h2 class="name">{{food.name}}</h2>
  							<P class="desc">{{food.description}}</P>
  							<div class="extra">
  								<span>月售{{food.sellCount}}份</span>
  								<span>好评率{{food.rating}}%</span>
  							</div>
  							<div class="price">
  								<span>￥{{food.price}}</span>
  								<span v-show="food.oldPrice">{{food.oldPrice}}</span>
  							</div>
                <!-- 引入cartcontrol组件 -->
                <div class="cartcontrol-wrapper">
                  <cartcontrol :food="food" @cartAdd="cartAdd"></cartcontrol>
                </div>   
  						</div>
  					</li>
  				</ul>
  			</li>
           
  		</ul>
  	</div>
  	<shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
    <!-- 商品详情页组件 -->
    <food :food="selectedFood" ref="food" @cartAdd="cartAdd"></food>
  </div>
</template>

<script>
// 引入better-scroll组件库
import BScroll from'better-scroll';
import shopcart from '../shopcart/shopcart.vue'
import cartcontrol from '../cartcontrol/cartcontrol.vue'
import food from '../food/food.vue'
export default {
   props:{
   	seller:{
   		type:Object
   	}
   },
   data(){
   	return {
     goods:[],
     // 存放每个区间的高度
     listHeight:[],
     scrollY:0,
     selectedFood:{},
   	}
   },
   computed:{
   	// 绑定左边菜单活动状态
      courrentIndex(){
      	for (let i=0;i< this.listHeight.length;i++){
      		let height1=this.listHeight[i];
      		let height2=this.listHeight[i+1];
      		if (!height2 || (this.scrollY>=height1 &&this.scrollY<height2)){
      			return i;
      		}
      	}
      	return 0;
      },
   /*   selectFoods(){
        let foods=[];
        this.goods.forEach((good)=>{
           good.foods.forEach((food)=>{
               if(food.count) {
                foods.push(food);
               }
           });
        });
        return foods;
      }*/
      selectFoods(){
      let foods=[];
      this.goods.forEach(function (good){
        good.foods.forEach(function (food){
          if (food.count){
            foods.push(food);
          }
        })
      })
       return foods;
     }
   },
   	// 从服务器获取数据
  created(){
    // this.$http.get('/api/goods')
    // 使用www.easy-mock.com的数据接口
    this.$http.get('https://www.easy-mock.com/mock/5ba742b7774ea0691ff86e85/api/goods')
    // 使用箭头函数
    .then((res)=>{
       res = res.body;
       // console.log(res)
       this.goods=res.data;
          // console.log(this.goods)
          /*渲染完后执行*/
        this.$nextTick(()=>{
          this.initScroll();
          this.calculateHeight();  
        })     
         },(err)=>{
        console.log(err)
    }),
    this.classMap=['decrease', 'discount','guarantee','invoice','special'];
  },
   methods:{
       initScroll(){
       	// 通过$refs获取DOM元素
       	// 设置BScroll参数click:true
            this.meunScroll = new BScroll(this.$refs.menuWrapper,{click:true});
            // probeType:3监听实时滚动的位置
            this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
            	probeType:3,click:true
            });
            // 监听滚动事件
            this.foodsScroll.on('scroll',(pos)=>{
            	this.scrollY = Math.abs(Math.round(pos.y));
            	
            })
    	},
    	/*获取个区间的高度*/
    	calculateHeight(){
    		// 获取DOM元素
    		let foodList = this.$refs.foodsWrapper.getElementsByClassName("food-list-hook");      
    		let height = 0 ;
    		this.listHeight.push(height);
    		
    		for(let i=0; i < foodList.length;i++){
    			let item =foodList[i];
    			// 通过clientHeight获取元素高度
    			height += item.clientHeight;
    			this.listHeight.push(height);
    		}
    		// console.log(this.listHeight)
    	},
    	/*为左边菜单绑定事件*/
       selectMenu(index){
     	
        let foodList = this.$refs.foodsWrapper.getElementsByClassName("food-list-hook");  
        let el = foodList[index];
        // 调用插件的scrollToElement方法跳转
        this.foodsScroll.scrollToElement(el, 300);
     },
     // 监听到子组件事件后执行的方法,获取当前点击的dom元素target
     cartAdd(target){
       // 调用子组件drop方法,抛物线小球
       this.$refs.shopcart.drop(target)
     },
     selectFood(food){
         this.selectedFood = food;
         this.$refs.food.show();
     }
    },
  components: {
  	shopcart,
    cartcontrol,
    food
  }
};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.goods{
      position: absolute;
      display: flex;
      width: 100%;
      top:174px;
      bottom: 46px;
      overflow: hidden;
}
.goods> .menu-wrapper{
     flex: 0 0 80px;
     width: 80px;
     background: #f3f5f7;
     
}
.menu-item{
     display:table;
     height: 54px;
     width: 56px;
     line-height: 14px;
     padding: 0 12px;
}
.current{
	position: relative;
	z-index: 10;
	margin-top: -1px;
	background: #fff;
	font-weight: 700;
}
 ul >.current > .text{
	border-bottom:none;
}
.text >.icon{
   display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin-right: 0px;
	background-size: 12px 12px;
	background-repeat: no-repeat;
}
/*活动图标样式*/
.text > .decrease{
  background-image: url(decrease_3@2x.png);
}
.text > .discount{
  background-image: url(discount_3@2x.png);
}
.text >.guarantee{
  background-image: url(guarantee_3@2x.png);
}
.text > .invoice{
  background-image: url(invoice_3@2x.png);
}
.text > .special{
  background-image: url(special_3@2x.png);
}
.menu-item>.text{
	font-size: 12px;
	display: table-cell;
	width: 56px;
	vertical-align: middle;
	text-align: center;
	border-bottom: rgba(7,17,27,0.1) 1px solid;
}
.goods> .foods-wrapper{
	flex: 1;
	
}
.food-list >.title{
	padding-left: 14px;
	height:24px;
	line-height: 26px;
	border-left: 2px solid #d9dde1;
	font-size: 12px;
	color: rgb(147,153,159);
	background: #f3f5f7;
}
.food-item{
	display: flex;
	margin: 18px 18px 0px 18px;
	padding-bottom: 18px;
	border-bottom: rgba(7,17,27,0.1) 1px solid;
}
.food-item:last-child{
	border-bottom: none;
	margin-bottom: 0px;
}
.food-list > ul >.food-item > .icon{
    flex:0 0 57px;
    margin-right: 10px;
}
.food-list > ul >.food-item > .icon >img{
   width: 57px;
   height: 57px;
}
.food-list > ul >.food-item > .content{
	flex:1;
  position: relative;
}
.food-list > ul >.food-item > .content > .name{
	margin:2px 0 8px 0;
	height: 14px;
	line-height:14px;
	color: rgb(7,17,27);
}
.food-list > ul >.food-item > .content >.desc,.food-list > ul >.food-item > .content >.extra{
	margin-bottom: 8px;
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
}
.food-list > ul >.food-item > .content >.desc{
	margin-bottom: 8px;
	line-height: 12px;
}
.food-list > ul >.food-item > .content >.extra > span:last-child{
    margin-left: 12px;
}
 .price > span{
	font-size: 14px;
	color: rgb(240,20,20);
	font-weight: 700;
	line-height: 24px;
}
.food-list > ul >.food-item > .content >.price > span:last-child{
	font-size: 10px;
	color: rgb(147,153,159);
	margin-left: 8px;
	text-decoration: line-through;
	font-weight: 700;
	line-height: 24px;
}
.cartcontrol-wrapper {
  position: absolute;
  right: 0;
  bottom:-5px;

}

