<template>
	<div class="header">
		<div class="content-wrapper">
			<div class="avatar">
				<img src="" width="64" height="64" :src="seller.avatar">
			</div>
			<div class="content">
				<div class="title">
					<span class="brand"></span>
					<span class="name">{{seller.name}}</span>
				</div>
				<div class="description">{{seller.description}}/{{seller.deliveryTime}}分钟送达</div>
				<!-- 先if判断是否存在seller.supports，再取seller.supports[0].description，因为数据是异步请求过来的，否则会报错。 -->
			<div class="support" v-if="seller.supports">
				<span class="icon" :class="classMap[seller.supports[0].type]"></span>
				<span class="text">{{seller.supports[0].description}}</span>
			</div>
			</div>
			<div v-if="seller.supports" class="supprot-count" @click="showDetail">
				<span class="count">{{seller.supports.length}}个</span> <i class="icon-keyboard_arrow_right" ></i>
			</div>
		</div>
		<div class="bulletin-wrapper" @click="showDetail">
			<span class="bulletin-title"></span>
			<span class="bulletin-text">{{seller.bulletin}}</span>
			<i class="icon-keyboard_arrow_right" ></i>
		</div>
		<div class="background">
			<img :src="seller.avatar" width="100%" height="100%">
		</div>
            <!-- 弹出层 -->
       <transition name="fade">
		<div v-show="detailShow" class="detail">
			<div class="detail-wrapper clearfix">
				<div class="detail-main">
					<h1 class="name">{{seller.name}}</h1>
					<!-- 引入星星评分组件 -->
					<div class="star-wrapper">
					  <star :size="48" :score="seller.score"></star>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">优惠信息</div>
						<div class="line"></div>
					</div>
					<ul v-if="seller.supports" class="supports">
						<li class="supports-item" v-for="(item,index) in seller.supports">
							<span class="icon" :class="classMap[seller.supports[index].type]"></span>
							<span class="text">{{seller.supports[index].description}}</span>
						</li>
					</ul>
					<div class="title">
						<div class="line"></div>
						<div class="text">商家公告</div>
						<div class="line"></div>
					</div>
					<div class="bulletin">
						<p class="content">{{seller.bulletin}}</p>
					</div>
				</div>
			</div>
			<div class="detail-close" @click="hideDetail"><i class="icon-close" ></i></div>
		</div>
	</transition>
	</div>
</template>
<script>
import star from '../star/star'
export default {
	props:{
		seller:{
			type:Object,
			default:null
		}
	},
	// 在模板渲染成html前调用，即通常初始化某些属性值，然后再渲染成视图。
	created(){
      this.classMap=['decrease', 'discount','guarantee','invoice','special']
	},
  components: {
       star
  },
  data() {
  	return{
  		detailShow:false,
  	}
  },
  methods:{
  	showDetail(){
  		this.detailShow=true
  	},
  	hideDetail(){
  	    this.detailShow=false
  	}
  }

};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
		color: #fff;
	    background:rgba(7,17,27,0.5);
        position: relative;
       overflow: hidden;
	}
.content-wrapper{
	padding: 24px 12px 18px 24px;
	/*消除缝隙*/
	font-size: 0;
    position: relative;
}
.avatar{
 display: inline-block;
 vertical-align: top;
}
.avatar > img {
	border-radius: 2px;
}
.content{
	display: inline-block;
	font-size: 16px;
	font-weight: bold;
	line-height: 18px;
	margin-left: 16px;
}
.content >.title{
	margin-top: 2px;
}
.content >.title > .brand {
	width: 30px;
	height:18px;
	display: inline-block;
	background-image: url(brand@2x.png);
	background-size: 30px 18px;
	vertical-align: top;
}
.content >.title > .name{
	margin-left: 6px;
	font-size: 16px;
	line-height: 18px;
	font-weight: bold;
}
.content > .description{
	margin-top: 8px;
	font-size: 12px;
    font-weight: 200;
    line-height: 12px;
}
.content > .support{
	margin-top: 10px;
	font-size: 10px;
	font-weight: 200;
	line-height: 12px;
}
.content > .support > .icon{
	display: inline-block;
	vertical-align: top;
	width: 12px;
	height: 12px;
	margin-right: 4px;
	background-size: 12px 12px;
	background-repeat: no-repeat;
}
/*活动图标样式*/
.content > .support > .decrease{
  background-image: url(decrease_1@2x.png);
}
.content > .support > .discount{
  background-image: url(discount_1@2x.png);
}.content > .support > .guarantee{
  background-image: url(guarantee_1@2x.png);
}
.content > .support > .invoice{
  background-image: url(invoice_1@2x.png);
}
.content > .support > .special{
  background-image: url(special_1@2x.png);
}

.supprot-count{
  position: absolute;
  right: 12px;
  bottom: 14px;
  padding: 0px 8px;
  height: 24px;
  background-color: rgba(0,0,0,0.2);
  border-radius: 14px;
  font-weight: 200;
  line-height: 24px;
  text-align: center;
}
.supprot-count .count{
  font-size: 10px;
  vertical-align: top;
}
/*字体图标*/
.supprot-count > .icon-keyboard_arrow_right {
	font-size: 10px;
	line-height: 26px;
	margin-left: 2px;
}
 .bulletin-wrapper{
 	position: relative;
	height:28px;
	line-height: 28px;
	padding: 0 22px 0 12px;
	background-color: rgba(7,17,27,0.2);
	text-overflow:ellipsis;
    white-space:nowrap; 
	overflow:hidden; 

} 
.bulletin-wrapper > .bulletin-title{
  display: inline-block;
  vertical-align: top;
  width: 22px;
  height: 12px;
  margin-left: 12px;
  margin-top: 8px;
  background-image: url(bulletin@2x.png);
  background-size:22px 12px;
}
.bulletin-text{
	vertical-align: top;
	margin-left: 4px;
	font-size: 10px;
    font-weight: 200;
    vertical-align: top;
}
.bulletin-wrapper >.icon-keyboard_arrow_right {
	position: absolute;
	font-size: 10px;
	right:12px;
	top: 10px;
}
.background{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
	/**/
-webkit-filter: blur(5px); 
 -moz-filter: blur(5px);
  -ms-filter: blur(5px);    
      filter: blur(5px); 
}
/*弹出层动画*/
.fade-enter-active, .fade-leave-active {
  transition: all 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
/*弹出层*/
.detail{
	position: fixed;
	z-index: 100;
	top:0;
	left:0;
	width: 100%;
	height: 100%;
	overflow: auto;
	background:rgba(7,17,27,0.8);
}
.detail-wrapper{
	min-height: 100%;
	width: 100%;
}
.detail-wrapper > .detail-main {
	margin-top: 64px;
	padding-bottom: 64px;
}
.detail-wrapper > .detail-main > .name{
   line-height: 16px;
   text-align: center;
   font-size: 16px;
   font-weight: 700;
}
.detail-main > .star-wrapper{
	margin-top: 18px;
    padding: 2px 0;
    text-align: center;
}
/* title使用flex布局 */
.detail-main > .title{
	display: flex;
	width: 80%;
	margin: 28px auto 24px auto;
}
.detail-main > .title > .line{
	flex: 1;
	position: relative;
	top: -6px;
	border-bottom: 1px solid rgba(255,255,255,0.2);
}
.detail-main > .title > .text {
	padding: 0 12px;
	font-size: 14px;
    font-weight: 700;
}
.detail-main >.supports{
	width: 80%;
	margin: 0 auto;

}
.detail-main >.supports > .supports-item{
	padding: 0 12px;
	margin-bottom: 12px;
	font-size: 0;
}
.detail-main >.supports > .supports-item:last-child{
	margin-bottom: 0px;
}
.detail-main >.supports > .supports-item >.icon{
	display: inline-block;
	width: 16px;
	height: 16px;
	vertical-align: top;
	margin-right: 6px;
	background-size: 16px 16px;
	background-repeat: no-repeat;
}
/*活动图标样式*/
.detail-main >.supports > .supports-item > .decrease{
  background-image: url(decrease_1@2x.png);
}
.detail-main >.supports > .supports-item > .discount{
  background-image: url(discount_1@2x.png);
}
.detail-main >.supports > .supports-item > .guarantee{
  background-image: url(guarantee_1@2x.png);
}
.detail-main >.supports > .supports-item >.invoice{
  background-image: url(invoice_1@2x.png);
}
.detail-main >.supports > .supports-item >.special{
  background-image: url(special_1@2x.png);
}
.detail-main >.supports > .supports-item > .text{
	font-size: 12px;
	line-height: 16px;
	vertical-align: top;
}
.detail-main > .bulletin{
	width: 80%;
	margin: 0 auto;
}
.detail-main > .bulletin >.content {
	padding: 0 12px;
	line-height: 24px;
	font-size: 12px;
}
.detail-close{
	position: relative;
	width: 32px;
	height:32px;
	margin:-64px auto 0 auto;
	clear: both;
	font-size: 32px;
}
.datail-close > .icon-close{

}
</style>


